'use client';

import { useEffect, useRef, useState } from 'react';
import Link from 'next/link';
import { SpaceInvadersGame } from '@/lib/games/spaceInvaders';

export default function SpaceInvadersPage() {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const gameRef = useRef<SpaceInvadersGame | null>(null);
  const [score, setScore] = useState(0);
  const [lives, setLives] = useState(3);
  const [level, setLevel] = useState(1);
  const [gameOver, setGameOver] = useState(false);
  const [gameStarted, setGameStarted] = useState(false);
  const [isPaused, setIsPaused] = useState(false);
  const [highScore, setHighScore] = useState(0);

  useEffect(() => {
    // Load high score from localStorage
    const savedHighScore = localStorage.getItem('space-invaders-high-score');
    if (savedHighScore) {
      setHighScore(parseInt(savedHighScore));
    }
  }, []);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    // Set canvas size
    canvas.width = 800;
    canvas.height = 600;

    const handleUpdate = (data: { score: number; lives: number; level: number }) => {
      setScore(data.score);
      setLives(data.lives);
      setLevel(data.level);
    };

    const handleGameOver = (finalScore: number) => {
      setGameOver(true);
      setGameStarted(false);
      
      // Update high score
      if (finalScore > highScore) {
        setHighScore(finalScore);
        localStorage.setItem('space-invaders-high-score', finalScore.toString());
      }
    };

    // Initialize game
    gameRef.current = new SpaceInvadersGame(canvas, handleUpdate, handleGameOver);

    // Keyboard controls
    const handleKeyDown = (e: KeyboardEvent) => {
      if (!gameRef.current) return;

      switch (e.key) {
        case ' ':
          e.preventDefault();
          if (gameStarted && !gameOver) {
            gameRef.current.shoot();
          } else if (!gameStarted && !gameOver) {
            setGameStarted(true);
            gameRef.current.start();
          }
          break;
        case 'p':
        case 'P':
          e.preventDefault();
          if (gameStarted) {
            gameRef.current.pause();
            setIsPaused(!isPaused);
          }
          break;
        case 'r':
        case 'R':
          e.preventDefault();
          handleRestart();
          break;
      }
    };

    window.addEventListener('keydown', handleKeyDown);

    return () => {
      window.removeEventListener('keydown', handleKeyDown);
      if (gameRef.current) {
        gameRef.current.destroy();
      }
    };
  }, [gameStarted, gameOver, isPaused, highScore]);

  const handleStart = () => {
    if (gameRef.current) {
      setGameStarted(true);
      gameRef.current.start();
    }
  };

  const handleRestart = () => {
    if (gameRef.current) {
      gameRef.current.restart();
      setScore(0);
      setLives(3);
      setLevel(1);
      setGameOver(false);
      setGameStarted(true);
      setIsPaused(false);
    }
  };

  const handlePause = () => {
    if (gameRef.current && gameStarted) {
      gameRef.current.pause();
      setIsPaused(!isPaused);
    }
  };

  const handleShoot = () => {
    if (gameRef.current && gameStarted && !gameOver && !isPaused) {
      gameRef.current.shoot();
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-900 to-black py-8 px-4">
      <div className="max-w-5xl mx-auto">
        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-blue-300 mb-2">Space Invaders</h1>
          <p className="text-blue-200">Usa las flechas para moverte y ESPACIO para disparar</p>
        </div>

        {/* Game Stats */}
        <div className="flex justify-center gap-6 mb-6">
          <div className="bg-gray-800 rounded-lg px-6 py-3 shadow-md border border-blue-500">
            <div className="text-sm text-blue-300">Puntuación</div>
            <div className="text-2xl font-bold text-green-400">{score}</div>
          </div>
          <div className="bg-gray-800 rounded-lg px-6 py-3 shadow-md border border-blue-500">
            <div className="text-sm text-blue-300">Vidas</div>
            <div className="text-2xl font-bold text-red-400">{lives}</div>
          </div>
          <div className="bg-gray-800 rounded-lg px-6 py-3 shadow-md border border-blue-500">
            <div className="text-sm text-blue-300">Nivel</div>
            <div className="text-2xl font-bold text-yellow-400">{level}</div>
          </div>
          <div className="bg-gray-800 rounded-lg px-6 py-3 shadow-md border border-blue-500">
            <div className="text-sm text-blue-300">Récord</div>
            <div className="text-2xl font-bold text-purple-400">{highScore}</div>
          </div>
        </div>

        {/* Game Canvas */}
        <div className="flex justify-center mb-6">
          <div className="relative">
            <canvas
              ref={canvasRef}
              className="border-4 border-blue-400 rounded-lg shadow-2xl"
            />
            
            {/* Game Over Overlay */}
            {gameOver && (
              <div className="absolute inset-0 bg-black bg-opacity-90 flex items-center justify-center rounded-lg">
                <div className="text-center text-white">
                  <h2 className="text-4xl font-bold mb-4 text-red-400">¡Invasión Fallida!</h2>
                  <p className="text-xl mb-2">Puntuación Final: <span className="text-green-400">{score}</span></p>
                  <p className="text-lg mb-2">Nivel Alcanzado: <span className="text-yellow-400">{level}</span></p>
                  {score === highScore && score > 0 && (
                    <p className="text-purple-400 mb-4 text-xl">¡Nuevo Récord Galáctico!</p>
                  )}
                  <button
                    onClick={handleRestart}
                    className="bg-blue-600 hover:bg-blue-700 text-white px-8 py-4 rounded-lg font-semibold transition-colors text-lg"
                  >
                    Defender de Nuevo
                  </button>
                </div>
              </div>
            )}

            {/* Start Game Overlay */}
            {!gameStarted && !gameOver && (
              <div className="absolute inset-0 bg-black bg-opacity-90 flex items-center justify-center rounded-lg">
                <div className="text-center text-white">
                  <h2 className="text-4xl font-bold mb-6 text-blue-400">¡La Invasión Comienza!</h2>
                  <p className="text-lg mb-4">Los alienígenas se acercan a la Tierra</p>
                  <p className="text-md mb-6 text-gray-300">Flechas: Mover • Espacio: Disparar</p>
                  <button
                    onClick={handleStart}
                    className="bg-green-600 hover:bg-green-700 text-white px-8 py-4 rounded-lg font-semibold transition-colors text-lg"
                  >
                    Comenzar Defensa
                  </button>
                </div>
              </div>
            )}

            {/* Pause Overlay */}
            {isPaused && gameStarted && (
              <div className="absolute inset-0 bg-black bg-opacity-90 flex items-center justify-center rounded-lg">
                <div className="text-center text-white">
                  <h2 className="text-3xl font-bold mb-4 text-yellow-400">Defensa Pausada</h2>
                  <p className="text-lg">Presiona P para continuar la batalla</p>
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Controls */}
        <div className="flex justify-center gap-4 mb-8">
          <button
            onClick={handlePause}
            disabled={!gameStarted || gameOver}
            className="bg-yellow-600 hover:bg-yellow-700 disabled:bg-gray-600 text-white px-6 py-3 rounded-lg font-semibold transition-colors"
          >
            {isPaused ? 'Reanudar' : 'Pausar'}
          </button>
          <button
            onClick={handleShoot}
            disabled={!gameStarted || gameOver || isPaused}
            className="bg-red-600 hover:bg-red-700 disabled:bg-gray-600 text-white px-6 py-3 rounded-lg font-semibold transition-colors"
          >
            Disparar
          </button>
          <button
            onClick={handleRestart}
            className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-lg font-semibold transition-colors"
          >
            Reiniciar
          </button>
        </div>

        {/* Instructions */}
        <div className="bg-gray-800 rounded-lg p-6 shadow-md mb-8 border border-blue-500">
          <h3 className="text-xl font-bold text-blue-300 mb-4">Instrucciones de Combate</h3>
          <div className="grid md:grid-cols-2 gap-6 text-gray-300">
            <div>
              <h4 className="font-semibold mb-3 text-blue-200">Controles:</h4>
              <ul className="space-y-2">
                <li>• <span className="text-green-400">Flechas Izq/Der o A/D:</span> Mover nave</li>
                <li>• <span className="text-red-400">Espacio:</span> Disparar láser</li>
                <li>• <span className="text-yellow-400">P:</span> Pausar/Reanudar</li>
                <li>• <span className="text-blue-400">R:</span> Reiniciar misión</li>
              </ul>
            </div>
            <div>
              <h4 className="font-semibold mb-3 text-blue-200">Objetivo:</h4>
              <ul className="space-y-2">
                <li>• <span className="text-green-400">Elimina todos los invasores</span></li>
                <li>• <span className="text-yellow-400">Evita sus disparos</span></li>
                <li>• <span className="text-red-400">No dejes que lleguen abajo</span></li>
                <li>• <span className="text-purple-400">Sobrevive el mayor tiempo posible</span></li>
              </ul>
            </div>
          </div>
          <div className="mt-4 p-4 bg-gray-700 rounded-lg">
            <h4 className="font-semibold mb-2 text-blue-200">Sistema de Puntuación:</h4>
            <p className="text-sm text-gray-300">
              Fila superior: <span className="text-red-400">50 pts</span> • 
              Segunda fila: <span className="text-orange-400">40 pts</span> • 
              Tercera fila: <span className="text-yellow-400">30 pts</span> • 
              Cuarta fila: <span className="text-green-400">20 pts</span> • 
              Fila inferior: <span className="text-blue-400">10 pts</span>
            </p>
          </div>
        </div>

        {/* Back Button */}
        <div className="text-center">
          <Link href="/games">
            <div className="inline-block bg-gray-800 text-white px-8 py-4 rounded-lg font-semibold hover:bg-gray-700 transition-colors border border-blue-500">
              Volver a Base de Operaciones
            </div>
          </Link>
        </div>
      </div>
    </div>
  );
}
