export interface Position {
  x: number;
  y: number;
}

export interface GameObject {
  x: number;
  y: number;
  width: number;
  height: number;
  active: boolean;
}

export interface Player extends GameObject {
  speed: number;
}

export interface Alien extends GameObject {
  points: number;
  row: number;
  col: number;
}

export interface Bullet extends GameObject {
  speed: number;
  isPlayerBullet: boolean;
}

export interface GameState {
  player: Player;
  aliens: Alien[];
  bullets: Bullet[];
  score: number;
  lives: number;
  level: number;
  gameOver: boolean;
  paused: boolean;
  alienDirection: number;
  alienSpeed: number;
  lastAlienShot: number;
}

export class SpaceInvadersGame {
  private canvas: HTMLCanvasElement;
  private ctx: CanvasRenderingContext2D;
  private gameState!: GameState;
  private animationId: number | null = null;
  private keys: { [key: string]: boolean } = {};
  private onUpdate: (data: { score: number; lives: number; level: number }) => void;
  private onGameOver: (score: number) => void;

  constructor(
    canvas: HTMLCanvasElement,
    onUpdate: (data: { score: number; lives: number; level: number }) => void,
    onGameOver: (score: number) => void
  ) {
    this.canvas = canvas;
    this.ctx = canvas.getContext('2d')!;
    this.onUpdate = onUpdate;
    this.onGameOver = onGameOver;

    this.initializeGame();
    this.setupEventListeners();
  }

  private initializeGame(): void {
    this.gameState = {
      player: {
        x: this.canvas.width / 2 - 25,
        y: this.canvas.height - 60,
        width: 50,
        height: 30,
        speed: 5,
        active: true
      },
      aliens: [],
      bullets: [],
      score: 0,
      lives: 3,
      level: 1,
      gameOver: false,
      paused: false,
      alienDirection: 1,
      alienSpeed: 1,
      lastAlienShot: 0
    };

    this.createAliens();
  }

  private createAliens(): void {
    this.gameState.aliens = [];
    const rows = 5;
    const cols = 10;
    const alienWidth = 40;
    const alienHeight = 30;
    const spacing = 10;
    const startX = 50;
    const startY = 50;

    for (let row = 0; row < rows; row++) {
      for (let col = 0; col < cols; col++) {
        this.gameState.aliens.push({
          x: startX + col * (alienWidth + spacing),
          y: startY + row * (alienHeight + spacing),
          width: alienWidth,
          height: alienHeight,
          active: true,
          points: (4 - row) * 10 + 10, // Top rows worth more points
          row,
          col
        });
      }
    }
  }

  private setupEventListeners(): void {
    const handleKeyDown = (e: KeyboardEvent) => {
      this.keys[e.key] = true;
    };

    const handleKeyUp = (e: KeyboardEvent) => {
      this.keys[e.key] = false;
    };

    window.addEventListener('keydown', handleKeyDown);
    window.addEventListener('keyup', handleKeyUp);
  }

  private updatePlayer(): void {
    if (this.keys['ArrowLeft'] || this.keys['a'] || this.keys['A']) {
      this.gameState.player.x = Math.max(0, this.gameState.player.x - this.gameState.player.speed);
    }
    if (this.keys['ArrowRight'] || this.keys['d'] || this.keys['D']) {
      this.gameState.player.x = Math.min(
        this.canvas.width - this.gameState.player.width,
        this.gameState.player.x + this.gameState.player.speed
      );
    }
  }

  private updateBullets(): void {
    this.gameState.bullets = this.gameState.bullets.filter(bullet => {
      if (!bullet.active) return false;

      if (bullet.isPlayerBullet) {
        bullet.y -= bullet.speed;
        if (bullet.y < 0) {
          bullet.active = false;
          return false;
        }
      } else {
        bullet.y += bullet.speed;
        if (bullet.y > this.canvas.height) {
          bullet.active = false;
          return false;
        }
      }

      return true;
    });
  }

  private updateAliens(): void {
    let moveDown = false;
    const activeAliens = this.gameState.aliens.filter(alien => alien.active);
    
    if (activeAliens.length === 0) {
      this.nextLevel();
      return;
    }

    // Check if aliens hit the edge
    const leftMost = Math.min(...activeAliens.map(alien => alien.x));
    const rightMost = Math.max(...activeAliens.map(alien => alien.x + alien.width));

    if (leftMost <= 0 || rightMost >= this.canvas.width) {
      this.gameState.alienDirection *= -1;
      moveDown = true;
    }

    // Move aliens
    this.gameState.aliens.forEach(alien => {
      if (!alien.active) return;
      
      alien.x += this.gameState.alienDirection * this.gameState.alienSpeed;
      if (moveDown) {
        alien.y += 20;
      }

      // Check if aliens reached the player
      if (alien.y + alien.height >= this.gameState.player.y) {
        this.gameOver();
      }
    });

    // Alien shooting
    const now = Date.now();
    if (now - this.gameState.lastAlienShot > 1000 + Math.random() * 2000) {
      this.alienShoot();
      this.gameState.lastAlienShot = now;
    }
  }

  private alienShoot(): void {
    const activeAliens = this.gameState.aliens.filter(alien => alien.active);
    if (activeAliens.length === 0) return;

    const shooter = activeAliens[Math.floor(Math.random() * activeAliens.length)];
    this.gameState.bullets.push({
      x: shooter.x + shooter.width / 2 - 2,
      y: shooter.y + shooter.height,
      width: 4,
      height: 10,
      speed: 3,
      active: true,
      isPlayerBullet: false
    });
  }

  private checkCollisions(): void {
    // Player bullets vs aliens
    this.gameState.bullets.forEach(bullet => {
      if (!bullet.active || !bullet.isPlayerBullet) return;

      this.gameState.aliens.forEach(alien => {
        if (!alien.active) return;

        if (this.isColliding(bullet, alien)) {
          bullet.active = false;
          alien.active = false;
          this.gameState.score += alien.points;
          this.onUpdate({
            score: this.gameState.score,
            lives: this.gameState.lives,
            level: this.gameState.level
          });
        }
      });
    });

    // Alien bullets vs player
    this.gameState.bullets.forEach(bullet => {
      if (!bullet.active || bullet.isPlayerBullet) return;

      if (this.isColliding(bullet, this.gameState.player)) {
        bullet.active = false;
        this.gameState.lives--;
        this.onUpdate({
          score: this.gameState.score,
          lives: this.gameState.lives,
          level: this.gameState.level
        });

        if (this.gameState.lives <= 0) {
          this.gameOver();
        }
      }
    });
  }

  private isColliding(obj1: GameObject, obj2: GameObject): boolean {
    return obj1.x < obj2.x + obj2.width &&
           obj1.x + obj1.width > obj2.x &&
           obj1.y < obj2.y + obj2.height &&
           obj1.y + obj1.height > obj2.y;
  }

  private nextLevel(): void {
    this.gameState.level++;
    this.gameState.alienSpeed += 0.5;
    this.createAliens();
    this.onUpdate({
      score: this.gameState.score,
      lives: this.gameState.lives,
      level: this.gameState.level
    });
  }

  private gameOver(): void {
    this.gameState.gameOver = true;
    this.onGameOver(this.gameState.score);
    if (this.animationId) {
      cancelAnimationFrame(this.animationId);
      this.animationId = null;
    }
  }

  private draw(): void {
    // Clear canvas
    this.ctx.fillStyle = '#000011';
    this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);

    // Draw stars
    this.ctx.fillStyle = '#ffffff';
    for (let i = 0; i < 50; i++) {
      const x = (i * 37) % this.canvas.width;
      const y = (i * 73) % this.canvas.height;
      this.ctx.fillRect(x, y, 1, 1);
    }

    // Draw player
    if (this.gameState.player.active) {
      this.ctx.fillStyle = '#00ff00';
      this.ctx.fillRect(
        this.gameState.player.x,
        this.gameState.player.y,
        this.gameState.player.width,
        this.gameState.player.height
      );
      
      // Player cannon
      this.ctx.fillRect(
        this.gameState.player.x + this.gameState.player.width / 2 - 3,
        this.gameState.player.y - 10,
        6,
        10
      );
    }

    // Draw aliens
    this.gameState.aliens.forEach(alien => {
      if (!alien.active) return;
      
      // Different colors for different rows
      const colors = ['#ff0000', '#ff8800', '#ffff00', '#00ff88', '#0088ff'];
      this.ctx.fillStyle = colors[alien.row] || '#ffffff';
      
      this.ctx.fillRect(alien.x, alien.y, alien.width, alien.height);
      
      // Simple alien design
      this.ctx.fillStyle = '#000000';
      this.ctx.fillRect(alien.x + 8, alien.y + 8, 6, 6);
      this.ctx.fillRect(alien.x + 26, alien.y + 8, 6, 6);
      this.ctx.fillRect(alien.x + 15, alien.y + 18, 10, 4);
    });

    // Draw bullets
    this.gameState.bullets.forEach(bullet => {
      if (!bullet.active) return;
      
      this.ctx.fillStyle = bullet.isPlayerBullet ? '#ffff00' : '#ff0000';
      this.ctx.fillRect(bullet.x, bullet.y, bullet.width, bullet.height);
    });
  }

  private gameLoop = (): void => {
    if (this.gameState.gameOver || this.gameState.paused) {
      return;
    }

    this.updatePlayer();
    this.updateBullets();
    this.updateAliens();
    this.checkCollisions();
    this.draw();

    this.animationId = requestAnimationFrame(this.gameLoop);
  };

  public start(): void {
    this.animationId = requestAnimationFrame(this.gameLoop);
  }

  public pause(): void {
    this.gameState.paused = !this.gameState.paused;
    if (!this.gameState.paused && !this.gameState.gameOver) {
      this.animationId = requestAnimationFrame(this.gameLoop);
    }
  }

  public shoot(): void {
    // Limit player bullets
    const playerBullets = this.gameState.bullets.filter(b => b.isPlayerBullet && b.active);
    if (playerBullets.length >= 3) return;

    this.gameState.bullets.push({
      x: this.gameState.player.x + this.gameState.player.width / 2 - 2,
      y: this.gameState.player.y - 10,
      width: 4,
      height: 10,
      speed: 7,
      active: true,
      isPlayerBullet: true
    });
  }

  public restart(): void {
    this.initializeGame();
    this.onUpdate({
      score: this.gameState.score,
      lives: this.gameState.lives,
      level: this.gameState.level
    });
    this.start();
  }

  public destroy(): void {
    if (this.animationId) {
      cancelAnimationFrame(this.animationId);
      this.animationId = null;
    }
  }

  public getGameState(): GameState {
    return { ...this.gameState };
  }
}

export function initSpaceInvadersGame(
  canvas: HTMLCanvasElement,
  onUpdate: (data: { score: number; lives: number; level: number }) => void,
  onGameOver: (score: number) => void
): SpaceInvadersGame {
  return new SpaceInvadersGame(canvas, onUpdate, onGameOver);
}
